html {
	overflow-y: scroll;
  }

html, body {
	display: block;
	background-color: #1f2833;
	color: #c5c6c7;
	width: 100%;
	height: 100%;
}

h1, h2, h3 {
	display: block;
	margin: 0;
	padding: 0 10px 20px 10px;
}

h4 {
	padding: 0 10px 0 10px;
}

p {
	display: block;
	margin: 0;
	padding: 0 10px 20px 10px;
	text-align: justify;
	text-justify: inter-word;
}

a {
	color: #45a29e;
	text-decoration: none;
}

a:hover {
	color: #2e6e6b;
}

// The Matrix animation
canvas {
	display: block;
	width: 100%;
	height: 100%;
}

.bg-custom {
	background-color: #010c10;
}

.bg-socials {
	background-color: #2e6e6b;
}

.grid-wrapper {
	display: grid;
    min-height: 100%;
    grid-template-rows: auto 1fr auto;
    grid-template-columns: 100%;
}

.header-container {
	display: flex;
	justify-content: center;
	height: 300px;
	width: 100%;
	// background-image: url("/assets/images/blue-background.png");
	background-color: #010c10;
	background-size: cover;
	background-attachment: fixed;
	background-position: bottom;
	// filter: blur(8px);
  	// -webkit-filter: blur(8px);
}

.header-container h1 {
	display: block;
	margin: auto;
	color: white;
	text-shadow: 3px 3px gray;
}

.main-content {
	display: block;
	padding: 20px;
}

